body {
  box-sizing: border-box;
  margin: 30px auto 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.people {
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  justify-content: center;
  gap: 30px;
  max-width: 1024px;
  margin-left: 55px;
  display: grid;
}

.person {
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  display: flex;
  position: relative;
}

.photo {
  width: 200px;
  height: 200px;
  position: relative;
  overflow: hidden;
}

img {
  border-radius: 50%;
  max-width: 100%;
  transition: transform 1s;
}

.rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  padding: 10px;
  font-size: 16px;
  transition: width 1s, height 1s;
  display: flex;
  position: absolute;
  top: 50%;
  left: 85%;
  transform: translate(-50%, -50%);
}

.details {
  text-align: center;
  position: relative;
}

.name {
  color: #333;
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  transition: font-size 1s, color 1s;
  position: relative;
}

.extra {
  opacity: 0;
  margin-top: 5px;
  transition: opacity 1s;
  display: flex;
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
}

.person:hover .extra {
  opacity: 1;
}

.sport, .pay {
  flex: 1;
  transition: font-size 1s, background-color 1s;
}

.pay:before {
  content: ",$";
}

.person:hover .sport, .pay {
  background-color: #eee;
}

.person:hover .name {
  color: #fc0;
  background-color: #eee;
  font-size: 24px;
}

.person:hover .rank {
  width: 19px;
  height: 19px;
}

.person:hover img {
  border-radius: 60px;
  transform: scale(1.1);
}

@media (width <= 750px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}
